import { sort } from 'utils/sort';
import { getItemLabel } from './getItemLabel';
export function getComputedReportItems({ report, idKey, labelKey = idKey, sortDirection = 0 /* asc */, sortKey = 'cost', }) {
    return sort(getUnsortedComputedReportItems({
        report,
        idKey,
        labelKey,
        sortDirection,
        sortKey,
    }), {
        key: sortKey,
        direction: sortDirection,
    });
}
export function getUnsortedComputedReportItems({ report, idKey, labelKey = idKey }) {
    if (!report) {
        return [];
    }
    const itemMap = new Map();
    const visitDataPoint = (dataPoint) => {
        if (dataPoint.values) {
            dataPoint.values.forEach((value) => {
                // clusters will either contain the cluster alias or default to cluster ID
                const cluster_alias = value.clusters && value.clusters.length > 0
                    ? value.clusters[0]
                    : undefined;
                const cluster = cluster_alias || value.cluster;
                const capacity = value.capacity ? value.capacity.value : 0;
                const cost = value.cost && value.cost.total ? value.cost.total.value : 0;
                const supplementary = value.supplementary && value.supplementary.total
                    ? value.supplementary.total.value
                    : 0;
                const infrastructure = value.infrastructure && value.infrastructure.usage
                    ? value.infrastructure.usage.value
                    : 0;
                // Ensure unique IDs -- https://github.com/project-koku/koku-ui/issues/706
                const idSuffix = idKey !== 'date' && idKey !== 'cluster' && value.cluster
                    ? `-${value.cluster}`
                    : '';
                const id = `${value[idKey]}${idSuffix}`;
                let label;
                const itemLabelKey = getItemLabel({ report, labelKey, value });
                if (itemLabelKey === 'cluster' && cluster_alias) {
                    label = cluster_alias;
                }
                else if (value[itemLabelKey] instanceof Object) {
                    label = value[itemLabelKey].value;
                }
                else {
                    label = value[itemLabelKey];
                }
                if (itemLabelKey === 'account' && value.account_alias) {
                    label = value.account_alias;
                }
                const limit = value.limit ? value.limit.value : 0;
                const request = value.request ? value.request.value : 0;
                const usage = value.usage ? value.usage.value : 0;
                const units = Object.assign({ cost: value.cost && value.cost.total ? value.cost.total.units : 'USD' }, (value.supplementary &&
                    value.supplementary.total && {
                    supplementaryCost: value.supplementary.total.units,
                }), (value.usage && { usage: value.usage.units }));
                if (!itemMap.get(id)) {
                    itemMap.set(id, {
                        capacity,
                        cluster,
                        clusters: value.clusters,
                        cost,
                        deltaPercent: value.delta_percent,
                        deltaValue: value.delta_value,
                        supplementary,
                        id,
                        infrastructure,
                        label,
                        limit,
                        request,
                        units,
                        usage,
                    });
                    return;
                }
                itemMap.set(id, Object.assign({}, itemMap.get(id), { capacity: itemMap.get(id).capacity + capacity, cost: itemMap.get(id).cost + cost, supplementary: itemMap.get(id).supplementary + supplementary, infrastructure: itemMap.get(id).infrastructure + infrastructure, limit: itemMap.get(id).limit + limit, request: itemMap.get(id).request + request, usage: itemMap.get(id).usage + usage }));
            });
        }
        for (const key in dataPoint) {
            if (dataPoint[key] instanceof Array) {
                return dataPoint[key].forEach(visitDataPoint);
            }
        }
    };
    if (report && report.data) {
        report.data.forEach(visitDataPoint);
    }
    return Array.from(itemMap.values());
}
