import endOfMonth from 'date-fns/end_of_month';
import format from 'date-fns/format';
import formatDate from 'date-fns/format';
import getDate from 'date-fns/get_date';
import getYear from 'date-fns/get_year';
import startOfMonth from 'date-fns/start_of_month';
import i18next from 'i18next';
import { getComputedReportItems, } from 'utils/computedReport/getComputedReportItems';
import { unitLookupKey, } from 'utils/formatValue';
export function transformReport(report, type = 1 /* daily */, key = 'date', reportItem = 'cost') {
    if (!report) {
        return [];
    }
    const items = {
        report,
        idKey: key,
        sortKey: 'id',
        sortDirection: 1 /* desc */,
    };
    const computedItems = getComputedReportItems(items);
    if (type === 1 /* daily */) {
        return computedItems.map(i => createDatum(i[reportItem], i, key, reportItem));
    }
    if (type === 2 /* monthly */) {
        return computedItems.map(i => createDatum(i[reportItem], i, key, reportItem));
    }
    return computedItems.reduce((acc, d) => {
        const prevValue = acc.length ? acc[acc.length - 1].y : 0;
        return [...acc, createDatum(prevValue + d[reportItem], d, key, reportItem)];
    }, []);
}
export function createDatum(value, computedItem, idKey = 'date', reportItem = 'cost') {
    const xVal = idKey === 'date' ? getDate(computedItem.id) : computedItem.label;
    const yVal = isFloat(value)
        ? parseFloat(value.toFixed(2))
        : isInt(value)
            ? value
            : 0;
    return {
        x: xVal,
        y: yVal,
        key: computedItem.id,
        name: computedItem.id,
        units: computedItem.units[reportItem],
    };
}
export function getDatumDateRange(datums, offset = 0) {
    if (!(datums && datums.length)) {
        const today = new Date();
        // If datums is empty, obtain the month based on offset (e.g., to show previous month in chart legends)
        if (offset) {
            today.setMonth(today.getMonth() - offset);
        }
        const firstOfMonth = startOfMonth(today);
        return [firstOfMonth, today];
    }
    const start = new Date(datums[0].key + 'T00:00:00');
    const end = new Date(datums[datums.length - 1].key + 'T00:00:00');
    return [start, end];
}
export function getDateRange(datums, firstOfMonth = true, lastOfMonth = false, offset = 0) {
    const [start, end] = getDatumDateRange(datums, offset);
    // Show the date range we are trying to cover (i.e., days 1-30/31)
    if (firstOfMonth && start.setDate) {
        start.setDate(1);
    }
    if (lastOfMonth && start.setDate) {
        const lastDate = endOfMonth(start).getDate();
        end.setDate(lastDate);
    }
    return [start, end];
}
export function getDateRangeString(datums, firstOfMonth = false, lastOfMonth = false, offset = 0) {
    const [start, end] = getDateRange(datums, firstOfMonth, lastOfMonth, offset);
    return i18next.t(`chart.date_range`, {
        count: getDate(end),
        endDate: formatDate(end, 'DD'),
        month: Number(formatDate(start, 'M')) - 1,
        startDate: formatDate(start, 'DD'),
        year: getYear(end),
    });
}
export function getMonthRangeString(datums, key = 'chart.month_legend_label', offset = 0) {
    const [start, end] = getDateRange(datums, true, false, offset);
    return [
        i18next.t(key, {
            month: Number(formatDate(start, 'M')) - 1,
        }),
        i18next.t(key, {
            month: Number(formatDate(end, 'M')) - 1,
        }),
    ];
}
export function getMaxValue(datums) {
    let max = 0;
    if (datums && datums.length) {
        datums.forEach(datum => {
            if (datum.y > max) {
                max = datum.y;
            }
        });
    }
    return max;
}
export function getTooltipContent(formatValue) {
    return function labelFormatter(value, unit = null, options = {}) {
        const lookup = unitLookupKey(unit);
        switch (lookup) {
            case 'core-hours':
            case 'hrs':
            case 'gb':
            case 'gb-hours':
            case 'gb-mo':
            case 'vm-hours':
                return i18next.t(`unit_tooltips.${lookup}`, {
                    value: `${formatValue(value, unit, options)}`,
                });
            default:
                return `${formatValue(value, unit, options)}`;
        }
    };
}
export function getTooltipLabel(datum, formatValue, formatOptions, idKey = 'date', units) {
    if (!datum.key) {
        return '';
    }
    if (idKey === 'date') {
        const date = format(datum.key, 'DD MMM YYYY');
        return `${date} ${formatValue(datum.y, units ? units : datum.units, formatOptions)}`;
    }
    return datum.key.toString();
}
export function getCostRangeString(datums, key = 'chart.cost_legend_label', firstOfMonth = false, lastOfMonth = false, offset = 0) {
    const [start, end] = getDateRange(datums, firstOfMonth, lastOfMonth, offset);
    return i18next.t(key, {
        count: getDate(end),
        endDate: formatDate(end, 'D'),
        month: Number(formatDate(start, 'M')) - 1,
        startDate: formatDate(start, 'D'),
        year: getYear(end),
    });
}
export function getUsageRangeString(datums, key = 'chart.usage_legend_label', firstOfMonth = false, lastOfMonth = false, offset = 0) {
    return getCostRangeString(datums, key, firstOfMonth, lastOfMonth, offset);
}
function isInt(n) {
    return Number(n) === n && n % 1 === 0;
}
function isFloat(n) {
    return Number(n) === n && n % 1 !== 0;
}
