function getValueForItem(item, options) {
    if (typeof item === 'string') {
        return item;
    }
    const sortKey = options.key;
    return item[sortKey];
}
export function sort(array, options) {
    const { direction = 0 /* asc */ } = options || {};
    return [...array].sort((a, b) => {
        const aVal = direction === 0 /* asc */
            ? getValueForItem(a, options)
            : getValueForItem(b, options);
        const bVal = direction === 0 /* asc */
            ? getValueForItem(b, options)
            : getValueForItem(a, options);
        if (aVal > bVal) {
            return -1;
        }
        if (aVal < bVal) {
            return 1;
        }
        return 0;
    });
}
