export const unitLookupKey = unit => {
    const lookup = unit ? unit.toLowerCase() : '';
    switch (lookup) {
        case 'usd':
        case 'gb':
        case 'gb-hours':
        case 'gb-mo':
        case 'core-hours':
        case 'hrs':
        case 'tag-mo':
        case 'vm-hours':
            return lookup;
        default:
            return '';
    }
};
export const formatValue = (value, unit, options = {}) => {
    const lookup = unitLookupKey(unit);
    const fValue = value || 0;
    switch (lookup) {
        case 'usd':
            return formatCurrency(fValue, lookup, options);
        case 'gb':
        case 'gb-hours':
        case 'gb-mo':
        case 'tag-mo':
        case 'vm-hours':
            return formatUsageGb(fValue, lookup, options);
        case 'core-hours':
        case 'hrs':
            return formatUsageHrs(fValue, lookup, options);
        default:
            return unknownTypeFormatter(fValue, lookup, options);
    }
};
const unknownTypeFormatter = (value, _unit, { fractionDigits = 0 } = {}) => {
    return value.toLocaleString('en', {
        minimumFractionDigits: fractionDigits,
        maximumFractionDigits: fractionDigits,
    });
};
export const formatCurrency = (value, unit, { fractionDigits = 2 } = {}) => {
    let fValue = value;
    if (!value) {
        fValue = 0;
    }
    return fValue.toLocaleString('en', {
        style: 'currency',
        currency: unit || 'USD',
        minimumFractionDigits: fractionDigits,
        maximumFractionDigits: fractionDigits,
    });
};
export const formatUsageGb = (value, _unit, { fractionDigits = 0 } = {}) => {
    return value.toLocaleString('en', {
        minimumFractionDigits: fractionDigits,
        maximumFractionDigits: fractionDigits,
    });
};
export const formatUsageHrs = (value, _unit, { fractionDigits = 0 } = {}) => {
    return value.toLocaleString('en', {
        minimumFractionDigits: fractionDigits,
        maximumFractionDigits: fractionDigits,
    });
};
